:root {
  --active-link: #2196f3;
  --color-menu: #ecdcab;
  --color-title: #fcfcf7;
  --border-bottom-curent: #bf932a;
  --hover-menu-btn: #9e6200;
  --transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
  --font-hand: "Dancing Script";
}

// * {
